import React from 'react';
import './NavBar.css';
import logo from './my-logo.png';

class NavBar extends React.Component {
  render() {
    return(
      <div>
        <div className="navbar">
          <div style={{textAlign: "left", display: "inline-block", float: "left"}}>
            <a href="#about">About</a>
            <a href="#section2">Work</a>
          </div>
          <div style={{textAlign: "right", display: "inline-block", float: "right"}}>
            <a href="#contact">Contact</a>
            <a href="#">Blog</a>
          </div>
        </div>
        <div className="center-logo">
          <a href="#about">
            <img alt="my-logo" src={logo} style={{height: "100px", width: "100px", border: "0"}} />
          </a>
        </div>
      </div>
    );
  }
}

export default NavBar;
