import React from 'react';
import './Content.css';
import dp from './smallDp.jpg';

class Content extends React.Component {
  render() {
    return(
      <div>
        <div id="about" className="row">
          <h2 style={{textAlign: "center"}}>ABOUT ME!!</h2>
          <h4 style={{textAlign: "center"}}>Allow me to introduce myself...</h4>
          <div>
            <img
              src={dp}
              style={{float: "left", height: "100px", width: "100px", padding: "10px"}}
            />
            <p>
              Hi!! I am a graduate student at the University of San Francisco pursuing a Master’s degree in Computer Science and am currently seeking an internship for Summer 2020. I am good at java and writing SQL queries and have developed RESTful APIs using the same. I am learning about GCP and to write programs using Apache Beam and Dataflow in java this semester. I am interested and am a good fit for a Backend position at present. I have also taken up courses in Artificial Intelligence and have a few projects under my belt related to it, as a result would be interested in a corresponding role as well.
            </p>
            <p>
              Given device profiles in JSON format, parse the JSON files to extract valid device profiles and aggregate the device profiles to get daily statistics, lifetime statistics and application statistics that were being used by the devices. This was done using Apache Beam, which would work in a distributed system parallelly. This was performed on bounded data, but the same pipeline can be used for streaming data, with Apache Beam's window feature.
            </p>
          </div>
        </div>
        <div id="contact" className="row">
          <h2 style={{textAlign: "center"}}>SAY HELLO!!</h2>
          <h4 style={{textAlign: "center"}}>Get in touch...</h4>
          <p>I'm currently available for freelance projects or full-time work. If you're interested in working with me, please get in touch using one of the methods below. Or feel free to just say hi. I like that too.</p>
          <div className="row">
            <div className="side">
              Follow me online!!!
            </div>
            <div className="main">
              <h3>Contact me directly!!</h3>
              <p>
                Mitali Sawant
                <br />
                San Francisco, California
                <br />
                mitali0087@gmail.com
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Content;
